    createRowButton: function(gridId, resourceType, typeLabel, resourceSelectedEvent, isViewpointClass) {
        return {
            buttonicon: 'swa-icon-create-resource',
            caption: '',
            cursor: 'pointer',
            title: 'Create ' + typeLabel,
            onClickButton: function() {
                if (
                    isViewpointClass == true &&
                    resourceType == 'http://edg.topbraid.solutions/model/EnumerationClass'
                ) {
                    swa.populateModalDialog(
                        'Creation of ' +
                            typeLabel +
                            'is not supported in EDG 5.3.2. <br> Import a codelist RDF graph instead.',
                        'Create not supported',
                        null
                    );
                } else {
                    var loadId = gridId + 'create-dialog';
                    var params = {
                        callback: 'swa.createResource',
                        label: 'Create ' + typeLabel,
                        loadId: loadId,
                        resourceSelectedEvent: resourceSelectedEvent,
                        resourceType: '<' + resourceType + '>',
                    };
                    swa.loadModalDialog('edg:CreateResourceDialogForTabularEditor', loadId, params, '680px', '400px');
                }
            },
        };
    },

    /**
     * Sets up a listener to a given input field so that whenever it changes
     * a new local name will be generated in the URI field.
     * Used by the CreateResourceDialog.
     * @param inputId -  the id of the label input field
     * @param uriFieldId - the id of the URI field
     * @param newResourceURI - proposed URI for the new resource
     * @param includeLabel - the label contributes to the URI
     */
    initCreateResourceLabelField: function(inputId, uriFieldId, newResourceURI, includeLabel) {
        var uriField = $('#' + uriFieldId);
        var neo = newResourceURI;
        uriField.val(neo);
        if (includeLabel) {
            edg.initCreateResourceLabelFieldEnter(inputId);
            $('#' + inputId)
                .off('input')
                .on(
                    'input',
                    function(neo) {
                        var label = $('#' + inputId)
                            .val()
                            .replace(/\s+/g, ' ')
                            .split(' ')
                            .join('_'); // Replace ' ' with '_'
                        label = label.split("'").join('_'); // Replace ' with '_'
                        // %-encode all but Unicode chars and A-Z a-z 0-9 - _ . ! ~ * ' ( )
                        var localName = label.replace(/[^a-zA-Z0-9_.!~*'()-]/g, (c) =>
                            c.charCodeAt(0) >= 0x80 ? c : encodeURIComponent(c)
                        );
                        var neo = neo + localName;
                        uriField.val(neo);
                    }.bind(null, neo)
                );
        }
    },

    /**
     *
     */

    initCreateResourceLabelFieldEnter: function(inputId) {
        $('#' + inputId).keydown(function(event) {
            if (event.which == 13) {
                $('#createResourceDialogOkButton').click();
            }
        });
    },

    /**
     * Updates the uri field after a type is selected by fetching the preamble
     * Used by the CreateResourceDialog.
     * @param inputId -  the id of the label input field
     * @param uriFieldId - the id of the URI field
     * @param selectedValue - the resource selected in the type dropdown
     * @param includeLabel - the label contributes to the URI
     *
     * example call:
     * edg:selectorUpdateForNewResourceField('label-input', 'uri-input',selectedValue,'{= ?useLabel }');
     */

    selectorUpdateForNewResourceField: function(inputId, uriFieldId, selectedValue, includeLabel) {
        var servletURL = swa.servlet;

        return axiosInstance
            .get(servletURL, {
                params: {
                    resourceType: '<' + selectedValue + '>',
                    _base: swa.queryGraphURI,
                    _format: 'json',
                    _snippet: true,
                    _viewClass: 'edg:GenerateNewResourceURI',
                },
            })
            .then((response) => {
                if (response.data.error) {
                    swa.populateModalDialog('Operation failed: ' + data.newResourceURI, 'Operation Failed', null);
                } else {
                    edg.initCreateResourceLabelField(inputId, uriFieldId, data.newResourceURI, includeLabel);
                }
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    /**
     * updateCreateResourceDialog
     */

    updateCreateResourceDialog: function() {
        if ($('#type-select')[0] && $('#type-select')[0].options) {
            let selectedIndex = $('#type-select')[0].options.selectedIndex;
            let $option = $('#type-select')[0].options[selectedIndex];
            let isAbstractClass = $option.getAttribute('data-abstract');
            let val = $('#type-select').val();

            if (isAbstractClass) {
                $('.btn-confirm').attr('disabled', true);
                $('.edg-create-instance').addClass('ui-helper-hidden-accessible');
            } else {
                $('.btn-confirm').attr('disabled', false);
                $('.edg-create-instance').removeClass('ui-helper-hidden-accessible');
            }

            if (val.indexOf('-') == 0) {
                swa.loadWithResource('create-resource-dialog-body', 'primaryKeyClass', val.substring(1));
            }
        }
    },
