import React from 'react';
import { AppContext, ResourceTableCell, DisplayUtil, ActionTable } from 'TB';
import * as FunctionUtils from 'FunctionUtils';
import memoize from 'memoize-one';

export default class InteroperableDownstreamDependenciesGadget extends React.Component {
    constructor(props) {
        super(props);

        this.currentDataFetch = null;

        this.state = {
            data: [],
            loading: false,
        };
    }

    componentDidMount() {
        this.loadData();
        this.context.getEventManager().subscribe('edit', this.handleEdit);
    }

    componentWillUnmount() {
        if (this.currentDataFetch) {
            this.currentDataFetch.cancel();
        }

        this.context.getEventManager().unsubscribe('edit', this.handleEdit);
    }

    setValues(values) {
        //no-op, required for the form contract
    }

    //Given a cell for a resource, returns a ResourceTableCell
    renderCell = ({ cell: { value } }) => {
        let node = value;
        if (node) {
            return <ResourceTableCell resource={node} showURIs={this.props.showURIs} />;
        }

        return null;
    };

    fetchData = () => {
        if (this.currentDataFetch) {
            this.currentDataFetch.cancel();
        }

        this.currentDataFetch = FunctionUtils.makeCancelable(
            this.context.swp({
                _viewClass: 'edg:DataTableResultsCallbackForInteroperableDownstreamDependency',
                resource: `<${this.props.focusNode.uri}>`,
            })
        );

        this.currentDataFetch.promise
            .then((response) => {
                this.setState({
                    data: response.data,
                    loading: false,
                });
            })
            .catch((err) => {
                console.error(err);
                this.currentDataFetch = null;
            });
    };

    filterMethod = memoize((rows, id, filterValue) => {
        rows = rows.filter((row) => {
            const rowValue = row.values[id];
           
            return String(rowValue.label)
                .toLowerCase()
                .includes(String(filterValue).toLowerCase());
        });
        return rows;
    });

    getColumns = () => {
        return [
            {
                Header: 'uri',
                accessor: 'uri',
                show: false,
            },
            {
                Header: 'Dependency Type',
                accessor: 'dependencyType',
                width: 200,
            },
            {
                Header: 'Downstream Degree',
                accessor: 'degree',
                width: 150,
            },
            {
                Header: 'Downstream Dependency',
                accessor: 'dependency',
                filter: this.filterMethod,
                Cell: this.renderCell,
                width: 175,
            },
            {
                Header: 'Part of',
                accessor: 'partOf.label',
                width: 125,
                Cell: ({ cell: { value } }) => {
                    return DisplayUtil.HTMLCell(value);
                },
            },
        ];
    };

    handleEdit = (edit) => {
        if (this.state.data) {
            let containsObject = this.state.data.some((d) => edit.containsSubject(d.uri));

            if (containsObject) {
                this.loadData();
            }
        }
    };

    loadData = () => {
        this.setState({ loading: true }, this.fetchData);
    };

    render() {
        return (
            <ActionTable
                defaultSortColumn={3}
                loading={this.state.loading}
                reloadData={this.loadData}
                columns={this.getColumns()}
                data={this.state.data}
                filterable={true}
            />
        );
    }
}

InteroperableDownstreamDependenciesGadget.contextType = AppContext;
