import React from 'react';
import { AppContext, AutoComplete, TBDialog, LabeledComponent, LangSelect, Edit } from 'TB';

const Cardinality = {
    uri: 'edg:Cardinality',
    label: 'Cardinality',
};

const LogicalEntity = {
    uri: 'edg:LogicalEntity',
    label: 'Logical Entity',
};

const LOGICAL_RELATION = 'http://edg.topbraid.solutions/model/LogicalRelation';

export default class CreateLogicalRelationshipDialog extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            enableOkButton: false,
            label: '',
            labelLang: '',
            sourceEntity: this.props.focusNode || null,
            sourceEntityCardinality: null,
            targetEntity: null,
            targetEntityCardinality: null,
            identifier: null,
        };
    }
    componentDidMount() {
        this.getIdentifer().then((id) => {
            this.setState({
                identifier: id,
            });
        });
    }

    getIdentifer = () => {
        let template = `SELECT ?identifier WHERE {
            BIND(edg:newResourceWithUUID(?resourceType, '') as ?identifier).
        }`;

        let q = {
            query: template,
            variables: {
                resourceType: { uri: LOGICAL_RELATION },
            },
        };

        return this.context.sparqlQuery(q).then((response) => {
            return response.bindings[0].identifier.uri;
        });
    };

    getInfo = () => {
        return 'This dialog produces a new ' + this.props.type.label + '.';
    };

    getTitle = () => {
        return 'Create ' + this.props.type.label;
    };

    createMutation = () => {
        // CreateLogicalRelationHandler
    };

    onOK = () => {
        let params = {
            _base: this.context.getGraph().getQueryGraphURI(),
            uri: this.state.identifier,
            label: this.state.label,
            resourceType: LOGICAL_RELATION,
            contextResource: this.state.sourceEntity.uri,
            source: this.state.sourceEntity.uri,
            sourceCardinality: this.state.sourceEntityCardinality.uri,
            target: this.state.targetEntity.uri,
            targetCardinality: this.state.targetEntityCardinality.uri,
            metadata: JSON.stringify({ createResource: this.state.identifier }),
        };

        if (this.state.labelLang && this.state.labelLang.length > 0) {
            params.labelLang = this.state.labelLang;
        }

        return swa
            .post('edg:CreateLogicalRelationHandler', params)
            .then((response) => {
                let { data } = response;

                if (data.error) {
                    throw data.error;
                }

                if (data.violations && data.violations.length > 0) {
                    throw response.violations;
                }

                if (data.warnings && data.warnings.length > 0) {
                    throw response.warnings;
                }

                if (this.props.then) {
                    return this.props.then({ uri: this.state.identifier });
                }
            })
            .catch((err) => {
                this.context.showException(err);
            });
    };

    enableOkButton = () => {
        return Boolean(
            this.state.label &&
                this.state.sourceEntity &&
                this.state.targetEntity &&
                this.state.sourceEntityCardinality &&
                this.state.targetEntityCardinality
        );
    };

    render() {
        return (
            <TBDialog
                title={this.getTitle()}
                info={this.getInfo()}
                enableOkButton={this.enableOkButton()}
                onOK={this.onOK}
                body={this.renderBody()}
            />
        );
    }

    renderBody = () => {
        return (
            <React.Fragment>
                <LabeledComponent label="Label:" info="">
                    <input
                        id="label-input"
                        autoComplete="off"
                        className="LabelInput form-control"
                        type="text"
                        onChange={this.updateLabel}
                        value={this.state.label}
                    />
                    <LangSelect onChange={this.updateLabelLang} value={this.state.labelLang} />
                </LabeledComponent>
                <LabeledComponent label="Source Entity:" info="">
                    <AutoComplete
                        type={LogicalEntity}
                        onSelect={this.handleSourceEntityChange}
                        value={this.state.sourceEntity}
                        disabled={this.props.hasOwnProperty('focusNode')}
                    />
                </LabeledComponent>
                <LabeledComponent label="Source Entity Cardinality:" info="">
                    <AutoComplete
                        type={Cardinality}
                        onSelect={this.handleSourceCardinalityChange}
                        value={this.state.sourceEntityCardinality}
                    />
                </LabeledComponent>
                <hr />
                <LabeledComponent label="Target Entity:" info="">
                    <AutoComplete
                        type={LogicalEntity}
                        onSelect={this.handleTargetEntityChange}
                        value={this.state.targetEntity}
                    />
                </LabeledComponent>
                <LabeledComponent label="Target Entity Cardinality:" info="">
                    <AutoComplete
                        type={Cardinality}
                        onSelect={this.handleTargetCardinalityChange}
                        value={this.state.targetEntityCardinality}
                    />
                </LabeledComponent>
                <LabeledComponent
                    label="Identifier:"
                    info="Identifiers for Logical Relation instances are generated using a prefix followed by a UUID."
                >
                    <span title={this.state.identifer} className="text-info">
                        {this.state.identifier}
                    </span>
                </LabeledComponent>
            </React.Fragment>
        );
    };

    updateLabel = (e) => {
        this.setState({
            label: e.target.value,
        });
    };

    updateLabelLang = (lang) => {
        this.setState({
            labelLang: lang,
        });
    };
    handleSourceEntityChange = (entity) => {
        this.setState({
            sourceEntity: entity,
        });
    };

    handleSourceCardinalityChange = (cardinality) => {
        this.setState({
            sourceEntityCardinality: cardinality,
        });
    };
    handleTargetEntityChange = (entity) => {
        this.setState({
            targetEntity: entity,
        });
    };
    handleTargetCardinalityChange = (cardinality) => {
        this.setState({
            targetEntityCardinality: cardinality,
        });
    };
}

CreateLogicalRelationshipDialog.contextType = AppContext;
