// JavaScript file for TopBraid EDG Exporters
import { axiosInstance } from 'utils';

var edgExporter = {
    load: function(pane) {
        return axiosInstance
            .get(swa.servlet, {
                params: {
                    _snippet: true,
                    _viewClass: pane,
                    _contextdebug: true,
                },
            })
            .then((response) => {
                swa.load('edg-importer-main-canvas', response.data);
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    clearSelection: function() {
        $('.swa-relevant-properties')
            .find('input[type="checkbox"]')
            .prop('checked', false);
    },

    selectAll: function() {
        $('.swa-relevant-properties')
            .find('input[type="checkbox"]')
            .prop('checked', true);
    },

    acceptSelection: function(callBack, projectGraph) {
        var checkedBoxes = $('.swa-relevant-properties').find(':checked'),
            jsonData = [];

        $.each(checkedBoxes, function() {
            var resource = $(this).attr('data-uri');

            jsonData.push({
                resource: resource,
            });
        });

        return axiosInstance
            .get(swa.servlet, {
                params: {
                    _snippet: true,
                    _viewClass: callBack,
                    resources: JSON.stringify(jsonData),
                    graph: projectGraph,
                    _contextdebug: false,
                },
            })
            .then((response) => {
                $('#transform-jobs')
                    .empty()
                    .append(response.data);
                $('#export-accordion').accordion({
                    active: 1,
                });
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    avro: function(pdm, projectGraph) {
        return axiosInstance
            .get(swa.servlet, {
                params: {
                    _snippet: true,
                    _viewClass: 'edg:ExportOneAvroSchema',
                    physicalModel: pdm,
                    graph: projectGraph,
                },
            })
            .then((response) => {
                $('#edg-exporter-canvas')
                    .empty()
                    .append(response.data);
                $('#export-accordion').accordion({
                    collapsible: true,
                    icons: false,
                    active: 0,
                });
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },
};

(function($) {
    $(document).ready(function() {
        var isAvroExportPage = $('[data-page-type*="avro-export"]').length > 0;
        if (isAvroExportPage) {
            var $selectMenu = $('.chosen-select');

            $('.chosen-select').chosen({
                disable_search_threshold: 8,
                no_results_text: 'No results found for',
            });

            if ($selectMenu.val() !== null) {
                var projectGraph = $('.chosen-select').attr('data-project-graph');
                edgExporter.avro($selectMenu.val(), projectGraph);
            }

            $selectMenu.on('change', function() {
                var $selectMenu = $(this),
                    projectGraph = $selectMenu.attr('data-project-graph');
                edgExporter.avro($selectMenu.val(), projectGraph);
            });
        }
    });
})(jQuery);

export default edgExporter;
