import { axiosInstance } from 'utils';

var datasetapp = {
    createEastPanelOptions: function(storageKey) {
        var result = {
            onclose: function() {
                swa.setStorageValue(storageKey + '-east-open', false);
                $('.datasetapp-form-maximize-button').css('display', '');
            },
            onopen: function() {
                swa.setStorageValue(storageKey + '-east-open', true);
                $('.datasetapp-form-maximize-button').css('display', 'none');
            },
            onresize_end: function(paneDirection, paneElement, paneState) {
                swa.setStorageValue(storageKey + '-east', paneState.size);
                swa.resizeGrid(paneDirection, paneElement);
            },
            size: swa.getStorageNumber(storageKey + '-east', 460),
            togglerTip_open: 'Hide Details',
            togglerTip_closed: 'Show Details',
        };

        if (swa.getStorageValue(storageKey + '-east-open') == 'false') {
            result.initClosed = true;
        } else {
            $('.datasetapp-form-maximize-button').css('display', 'none');
        }
        return result;
    },

    createTreeNode: function(resourceType, parentResource, property, inverse) {
        return axiosInstance
            .get(swa.servlet, {
                params: {
                    _base: swa.queryGraphURI,
                    _viewClass: 'datasetapp:CreateResourceService',
                    parent: '<' + parentResource + '>',
                    parentProperty: '<' + property + '>',
                    parentPropertyInverse: inverse,
                    resourceType: '<' + resourceType + '>',
                },
            })
            .then((response) => {
                swa.processEdits(response.data, false);
                gadgets.Hub.publish(
                    'org.datasetapp.instanceSelected',
                    data.metadata.newResource.substring(1, response.data.metadata.newResource.length - 1)
                );
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    deleteForm: function(classURI) {
        result = swa.populateConfirmDilaog(
            'Are you sure you want to replace the customized form layout for this class with the default (or inherited) form?'
        );
        Promise.resolve(result).then(
            function() {
                return axiosInstance
                    .get(swa.servlet, {
                        params: {
                            _viewClass: 'swaeditor:DeleteForm',
                            class: '<' + classURI + '>',
                        },
                    })
                    .then((response) => {
                        swa.populateModalDialog('Form has been reset.', null, null);
                    })
                    .catch((err) => {
                        swa.populateModalDialog('Operation failed: ' + errorThrown, 'Operation failed', null);
                    });
            },
            function(err) {
                //dialog canceled
            }
        );
    },

    isFormVisible: function() {
        return $('.datasetapp-form-maximize-button').css('display') == 'none';
    },

    maximizeForm: function(formId) {
        var selector = 'body';
        if (formId && formId.length > 0) {
            selector = '#' + formId;
        }
        $(selector)
            .layout()
            .toggle('east');
    },

    minimizeForm: function(formId) {
        var selector = 'body';
        if (formId && formId.length > 0) {
            selector = '#' + formId;
        }
        $(selector)
            .layout()
            .toggle('east');
    },

    navigateTo: function(graphId, tagId, classURI, treeMode, treeProperty) {
        var uri = graphId + (tagId != '' ? '.' + tagId : '') + '.editor?resourceType=' + encodeURIComponent(classURI);
        if (treeMode) {
            uri += '&treeMode=true';
        }
        if (treeProperty) {
            uri += '&treeProperty=' + encodeURIComponent(treeProperty);
        }
        document.location.href = uri;
    },
};

export default datasetapp;
