import React from 'react';
import { AppContext, ResourceTableCell, CreateResourceDialogs, ActionTable } from 'TB';
import * as FunctionUtils from 'FunctionUtils';
import memoize from 'memoize-one';

export default class LogicalEntityAttributesGadget extends React.Component {
    constructor(props) {
        super(props);

        this.currentDataFetch = null;

        this.state = {
            data: [],
            loading: false,
        };
    }

    componentDidMount() {
        this.loadData();
        this.context.getEventManager().subscribe('edit', this.handleEdit);
    }

    componentWillUnmount() {
        if (this.currentDataFetch) {
            this.currentDataFetch.cancel();
        }

        this.context.getEventManager().unsubscribe('edit', this.handleEdit);
    }

    setValues(values) {
        //no-op, required for the form contract
    }

    //Given a cell for a resource, returns a ResourceTableCell
    renderCell = ({ cell: { value } }) => {
        let node = value;
        if (node) {
            return <ResourceTableCell resource={node} showURIs={this.props.showURIs} />;
        }

        return null;
    };

    fetchData = () => {
        if (this.currentDataFetch) {
            this.currentDataFetch.cancel();
        }

        this.currentDataFetch = FunctionUtils.makeCancelable(
            this.context.swp({
                _viewClass: 'edg:DataTableResultsCallbackForLogicalEntityAttributes',
                resource: `<${this.props.focusNode.uri}>`,
            })
        );

        this.currentDataFetch.promise
            .then((response) => {
                this.setState({
                    data: response.data,
                    loading: false,
                });
            })
            .catch((err) => {
                console.error(err);
                this.currentDataFetch = null;
            });
    };

    filterMethod = memoize((rows, id, filterValue) => {
        rows = rows.filter((row) => {
            const rowValue = row.values[id];

            return String(rowValue.label)
                .toLowerCase()
                .includes(String(filterValue).toLowerCase());
        });
        return rows;
    });
    getColumns = () => {
        return [
            {
                Header: 'uri',
                accessor: 'uri',
                show: false,
            },
            {
                Header: 'Attribute',
                accessor: 'attribute',
                filter: this.filterMethod,
                Cell: this.renderCell,
                width: 150,
            },
            {
                Header: 'Datatype',
                accessor: 'datatype',
                filter: this.filterMethod,
                Cell: this.renderCell,
                width: 125,
            },
            {
                Header: 'Primary Key',
                accessor: 'primaryKey',
                Cell: ({ cell: { value } }) => {
                    return value != null ? String(value) : '';
                },
                width: 150,
            },
            {
                Header: 'Foreign Key',
                accessor: 'foreignKey',
                Cell: ({ cell: { value } }) => {
                    return value != null ? String(value) : '';
                },
                width: 150,
            },
            {
                Header: 'Nullable',
                accessor: 'nullable',
                Cell: ({ cell: { value } }) => {
                    return value != null ? String(value) : '';
                },
                width: 125,
            },
        ];
    };

    getActionButtons = () => {
        return [
            {
                label: 'New',
                action: (e, tableInst) => {
                    CreateResourceDialogs.createInstance(
                        this.context,
                        { uri: 'http://edg.topbraid.solutions/model/LogicalAttribute', label: 'Logical Attribute' },
                        this.onCreateNew,
                        { focusNode: this.props.focusNode }
                    );
                },
            },
        ];
    };

    handleEdit = (edit) => {
        if (this.state.data) {
            let containsObject = this.state.data.some((d) => edit.containsSubject(d.uri));

            if (containsObject) {
                this.loadData();
            }
        }
    };

    loadData = () => {
        this.setState({ loading: true }, this.fetchData);
    };

    render() {
        return (
            <div className="box-rounded">
                <div className="size18px dark-blue">
                    Attributes for <strong>{this.props.focusNode.label}</strong>{' '}
                </div>
                <ActionTable
                    defaultSortColumn={1}
                    loading={this.state.loading}
                    reloadData={this.loadData}
                    columns={this.getColumns()}
                    data={this.state.data}
                    filterable={true}
                    actionButtons={this.getActionButtons()}
                />
            </div>
        );
    }
}

LogicalEntityAttributesGadget.contextType = AppContext;
