import React from 'react';
import { AppContext, AutoComplete, TBDialog, LabeledComponent, LangSelect, Edit } from 'TB';

const SOURCE = {
    uri: 'edg:Interoperable',
    label: 'Source',
};

const TARGET = {
    uri: 'edg:Interoperable',
    label: 'Target',
};

export default class CreateLogicalAttributeDialog extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            enableOkButton: false,
            label: '',
            labelLang: '',
            source: null,
            targt: null,
        };
    }
    componentDidMount() {
        this.getIdentifer().then((id) => {
            this.setState({
                identifier: id,
            });
        });
    }

    getIdentifer = () => {
        let template = `SELECT ?identifier WHERE {
            BIND(edg:newResourceWithUUID(?resourceType, '') as ?identifier).
        }`;

        let q = {
            query: template,
            variables: {
                resourceType: { uri: this.props.type.uri },
            },
        };

        return this.context.sparqlQuery(q).then((response) => {
            return response.bindings[0].identifier.uri;
        });
    };

    getInfo = () => {
        return 'This dialog produces a new ' + this.props.type.label + '.';
    };

    getTitle = () => {
        return 'Create ' + this.props.type.label;
    };

    createMutation = () => {
        // CreateLogicalRelationHandler
    };

    onOK = () => {
        let params = {
            _base: this.context.getGraph().getQueryGraphURI(),
            uri: this.state.identifier,
            label: this.state.label,
            resourceType: this.props.type.uri,
            source:this.state.source.uri,
            target:this.state.target.uri,
            metadata: JSON.stringify({ createResource: this.state.identifier }),
        };

        if (this.state.labelLang && this.state.labelLang.length > 0) {
            params.labelLang = this.state.labelLang;
        }

        return swa
            .post('edg:CreateFlowResourceHandler', params)
            .then((response) => {
                let { data } = response;

                if (data.error) {
                    throw data.error;
                }

                if (data.violations && data.violations.length > 0) {
                    throw response.violations;
                }

                if (data.warnings && data.warnings.length > 0) {
                    throw response.warnings;
                }

                if (this.props.then) {
                    return this.props.then({ uri: this.state.identifier });
                }
            })
            .catch((err) => {
                this.context.showException(err);
            });
    };

    enableOkButton = () => {
        return Boolean(this.state.label && this.state.source && this.state.target);
    };

    render() {
        return (
            <TBDialog
                title={this.getTitle()}
                info={this.getInfo()}
                enableOkButton={this.enableOkButton()}
                onOK={this.onOK}
                body={this.renderBody()}
            />
        );
    }

    renderBody = () => {
        return (
            <React.Fragment>
                <LabeledComponent label="Label:" info="">
                    <input
                        id="label-input"
                        autoComplete="off"
                        className="LabelInput form-control"
                        type="text"
                        onChange={this.updateLabel}
                        value={this.state.label}
                    />
                    <LangSelect onChange={this.updateLabelLang} value={this.state.labelLang} />
                </LabeledComponent>
                <LabeledComponent label="Source:" info="">
                    <AutoComplete type={SOURCE} onSelect={this.handleSourceChange} value={this.state.source} />
                </LabeledComponent>
                <LabeledComponent label="Target:" info="">
                    <AutoComplete type={TARGET} onSelect={this.handleTargetChange} value={this.state.target} />
                </LabeledComponent>
                <LabeledComponent
                    label="Identifier:"
                    info={
                        <span>
                            Identifiers for <strong>{this.props.type.label}</strong> instances are generated using a
                            prefix followed by a UUID.
                        </span>
                    }
                >
                    <span title={this.state.identifer} className="text-info">
                        {this.state.identifier}
                    </span>
                </LabeledComponent>
            </React.Fragment>
        );
    };

    updateLabel = (e) => {
        this.setState({
            label: e.target.value,
        });
    };

    updateLabelLang = (lang) => {
        this.setState({
            labelLang: lang,
        });
    };

    handleSourceChange = (source) => {
        this.setState({
            source: source,
        });
    };

    handleTargetChange = (target) => {
        this.setState({
            target: target,
        });
    };
}

CreateLogicalAttributeDialog.contextType = AppContext;
