import * as swa from 'swa';
import datasetapp from './datasetapp';

// @Override
swa.openEditResourceDialog = function(resource) {
    if (!datasetapp.isFormVisible()) {
        datasetapp.maximizeForm();
    }
    gadgets.Hub.publish('org.datasetapp.instanceSelected', resource);
};

// @Override
swa.openSwitchableResourceDialog = swa.openViewResourceDialog = function(resource) {
    if (!datasetapp.isFormVisible()) {
        datasetapp.maximizeForm();
    }
    gadgets.Hub.publish('org.datasetapp.instanceSelected', resource);
};
