import { axiosInstance } from 'utils';

var edgImporter = {
    // listeners

    init: function() {
        //    $(document).on('click', '.edgQAhomelink', function () {
        //      var response;
        //      edgqa.load('edg-qa:ProjectTypes');
        //     });

        $(document).on('click', '.edg-something', function() {
            edgqa.navigateTo('edg-importer:Run');
        });
    },

    load: function(pane) {
        return axiosInstance
            .get(swa.servlet, {
                params: {
                    _snippet: true,
                    _viewClass: pane,
                    _contextdebug: true,
                },
            })
            .then((response) => {
                swa.load('edg-importer-main-canvas', response.data);
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    navigateTo: function(resource) {
        window.location = swa.server + swa.servlet + '?_viewClass=' + resource;
    },
};
