import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import ResultsTableComponent from 'ResultsTableComponent';
import DataProviders from 'DataProviders';
import AdvancedSearchComponent from 'AdvancedSearch';
import SearchContext from 'SearchContext';
import SearchFiltersComponent from 'SearchFiltersComponent';
import * as searchHelper from 'SearchHelper';
import TBBreakout from 'TBBreakout';
import * as swa from 'swa';
import * as TBUtil from 'utils';

import * as ComponentFactory from 'ComponentFactory';

import SearchState from 'SearchState';
import store from 'store';

class ResultsAppComponent extends React.Component {
    constructor(props) {
        super(props);
        this.resultsRef = React.createRef();

        this.store = props.store;

        this.addHashListener = this.addHashListener.bind(this);
        this.removeHashListener = this.removeHashListener.bind(this);
        this.hashChanged = this.hashChanged.bind(this);
        this.handleLocalSearchChange = this.handleLocalSearchChange.bind(this);
        this.handleSearchChange = this.handleSearchChange.bind(this);
        this.handleSearchRequest = this.handleSearchRequest.bind(this);

        let search = new SearchState({
            resourceType: props.resourceType,
            filters: props.filters,
            columns: props.columns,
            isLocal: props.isLocal,
            maxRows: props.maxRows,
            updateDelegate: this.handleSearchChange,
            searchDelegate: this.handleSearchRequest,
        });

        this.state = {
            search: search,
            advancedColumns: [],
            hasAssetSelector: props.hasAssetSelector,
            hasHierarchyViewButton: props.hasHierarchyViewButton,
            currentUserIsManager: props.currentUserIsManager,
            advancedSearch: this.store.getState().advancedSearch,
        };

        this.renderResultsTable = this.renderResultsTable.bind(this);
        this.getSearchComponent = this.getSearchComponent.bind(this);
        this.handleResourceChange = this.handleResourceChange.bind(this);

        this.handleSetAdvancedColumns = this.handleSetAdvancedColumns.bind(this);

        this.updateComponentState = this.updateComponentState.bind(this);
        this.unsubscribe = this.store.subscribe(this.updateComponentState);
    }

    updateComponentState() {
        let newState = {};

        const { advancedSearch } = this.store.getState();

        if (this.state.advancedSearch !== advancedSearch) {
            newState.advancedSearch = advancedSearch;
            newState.advancedColumns = [];
        }

        if (Object.keys(newState).length > 0) {
            this.setState(newState);
        }
    }

    handleSearchChange(search) {
        if (search) {
            this.setState({
                search: search,
            });
        }
    }

    handleSearchRequest() {
        if (this.resultsRef) {
            this.resultsRef.current.requestSearch();
        }
    }

    handleResourceChange(resourceURI, bypassDefault) {
        DataProviders.UNIFIED.getDefaultSearch(resourceURI, this.props.GQLClient, bypassDefault)
            .then((appState) => {
                let error = null;
                if (
                    (appState.hasOwnProperty('resourceType') && appState.resourceType == null) ||
                    (appState.hasOwnProperty('selectedResourceType') && appState.selectedResourceType == null)
                ) {
                    error = (
                        <span>
                            <strong>{resourceURI}</strong>
                            &nbsp;does not have an accessible schema and cannot be used in the search utility.
                        </span>
                    );
                }

                this.setState(
                    {
                        error: error,
                    },
                    () => {
                        this.state.search.setState(appState);
                        let newSearch = TBUtil.updateQueryStringParameter(
                            window.location.search,
                            'resourceType',
                            resourceURI
                        );

                        if (newSearch) {
                            history.replaceState(null, null, newSearch);
                        }
                    }
                );
            })
            .catch((err) => {
                //Only retry once, if bypassDefault is true, don't attempt a recovery
                if (err === 'BAD_APPSTATE' && !bypassDefault) {
                    searchHelper.handleBadDefaultState(err, () => {
                        //Failed to load the default due to a bad state, try again without the defaults
                        this.handleResourceChange(resourceURI, true);
                    });
                } else {
                    swa.populateModalDialog(`Restoring Default Search failed:  ${err}`, 'Operation failed', null);
                }
            });
    }

    handleLocalSearchChange(localOnly) {
        this.state.search.setIsLocal(localOnly);
    }

    addHashListener() {
        window.addEventListener('hashchange', this.hashChanged, true);
    }

    removeHashListener() {
        window.removeEventListener('hashchange', this.hashChanged, true);
    }

    hashChanged() {
        sendHashToEditor();
    }

    handleSetAdvancedColumns(columns) {
        this.setState({
            advancedColumns: columns,
        });
    }

    renderResultsTable() {
        return (
            <SearchContext.Consumer>
                {(search) => (
                    <ResultsTableComponent
                        ref={this.resultsRef}
                        search={search}
                        columns={
                            this.state.advancedSearch
                                ? this.state.advancedColumns.length > 0
                                    ? this.state.advancedColumns
                                    : search.getAllColumns()
                                : search.getAllColumns()
                        }
                        advancedSearch={this.state.advancedSearch}
                        GQLClient={this.props.GQLClient}
                        resourceType={search.resourceType}
                        store={this.props.store}
                        domReference={this.props.resultsTableElement ? this.props.resultsTableElement : null}
                        tableOptions={this.props.resultsTableOptions}
                        baseGraph={this.props.baseGraph}
                        appName={this.props.appName}
                        simpleSearch={this.props.simpleSearch}
                        localSearch={search.isLocal}
                    />
                )}
            </SearchContext.Consumer>
        );
    }

    getSearchComponent() {
        if (this.state.advancedSearch === true) {
            return (
                <AdvancedSearchComponent
                    resultsRef={this.resultsRef}
                    store={this.store}
                    setColumns={this.handleSetAdvancedColumns}
                    columns={this.state.search.columns}
                    graphId={this.props.graphId}
                    tagId={this.props.tagId}
                    maxRows={this.props.maxRows}
                />
            );
        }

        return (
            <SearchFiltersComponent
                store={this.store}
                search={this.state.search}
                simpleSearch={this.props.simpleSearch}
                GQLClient={this.props.GQLClient}
                appName={this.props.appName}
                baseGraph={this.props.baseGraph}
                hasAssetSelector={this.state.hasAssetSelector}
                hasHierarchyViewButton={this.props.hasHierarchyViewButton}
                currentUserIsManager={this.state.currentUserIsManager}
                onResourceSelected={this.handleResourceChange}
                onLocalResultsChange={this.handleLocalSearchChange}
                disableLocalSearch={this.props.disableLocalSearch}
                actionsBar={() => {
                    return (
                        <React.Fragment>
                            {this.props.simpleSearch
                                ? null
                                : ComponentFactory.getAdvancedSearchButtonComponent(
                                      this.props.simpleSearch,
                                      this.props.store
                                  )}
                            {this.props.simpleSearch
                                ? null
                                : ComponentFactory.getFiltersListComponent(
                                      this.props.GQLClient,
                                      this.props.store,
                                      this.props.baseGraph,
                                      false
                                  )}
                            {ComponentFactory.getColumnsListComponent(
                                this.props.GQLClient,
                                this.props.store,
                                this.props.baseGraph,
                                this.state.search
                            )}
                            {ComponentFactory.getActionMenuButtonComponent(
                                false,
                                this.props.GQLClient,
                                this.props.appName,
                                this.props.baseGraph,
                                this.props.store,
                                this.state.search.maxRows,
                                this.props.currentUserIsManager
                            )}
                            {ComponentFactory.getHierarchyViewButtonComponent(this.state.search.resourceType)}
                        </React.Fragment>
                    );
                }}
            />
        );
    }

    componentDidMount() {
        this.addHashListener();
    }

    componentWillUnmount() {
        if (this.unsubscribe) {
            this.unsubscribe();
        }

        this.removeHashListener();
    }

    render() {
        if (this.state.error) {
            return <span className="alert alert-danger">{this.state.error}</span>;
        }

        return (
            <SearchContext.Provider value={this.state.search}>
                {this.getSearchComponent()}
                <TBBreakout el={this.props.resultsTableElement}>{this.renderResultsTable()}</TBBreakout>
            </SearchContext.Provider>
        );
    }
}

ResultsAppComponent.defaultProps = {
    simpleSearch: false,
};

ResultsAppComponent.propTypes = {
    simpleSearch: PropTypes.bool,
};

const initResultsApp = (appData, appContainer) => {
    let hasComponents = {};
    let doesPageHaveElement = document.getElementById('view-point-selector') ? true : false;

    hasComponents.hasAssetSelector = doesPageHaveElement;

    hasComponents.resultsTableElement = document.getElementById('tb-results-table');
    hasComponents.resultsTableOptions = searchHelper.getResultsTableData();

    doesPageHaveElement = document.getElementById('hierarchyView') ? true : false;
    hasComponents.hasHierarchyViewButton = doesPageHaveElement;

    ReactDOM.render(
        <ResultsAppComponent
            {...hasComponents}
            GQLClient={appData.GQLClient}
            resourceType={appData.resourceType}
            columns={appData.columns}
            filters={appData.filters}
            baseGraph={appData.baseGraph}
            appName={appData.appName}
            store={appData.store}
            graphId={appData.graphId}
            tagId={appData.tagId}
            maxRows={appData.maxRows}
            currentUserIsManager={appData.currentUserIsManager}
            simpleSearch={appData.simpleSearch}
            disableLocalSearch={appData.disableLocalSearch}
            isLocal={appData.isLocal || appData.localSearchState}
            projectGraph={appData.projectGraph}
        />,
        appContainer
    );
};

const initView = (element, bypassDefault) => {
    let doesPageHaveElement = element ? true : false;

    if (doesPageHaveElement) {
        let appData = searchHelper.getFiltersAppDataFromDOM(element);

        let GQLClient = DataProviders.GraphQL.getGQLClient(appData.graphId, appData.tagId);

        let getDefaults = null;

        if (appData.staticQuery) {
            getDefaults = searchHelper.getAppStateFromQuery(
                appData.staticQuery,
                GQLClient,
                DataProviders.GraphQL.buildGraphqlURL(appData.graphId, appData.tagId, appData.qname)
            );
        } else if (!appData.resourceType) {
            getDefaults = Promise.resolve();
        } else {
            getDefaults = DataProviders.UNIFIED.getDefaultSearch(appData.resourceType, GQLClient, bypassDefault).then(
                (appState) => {
                    if (appState.selectedResourceType === null && appData.resourceType != null) {
                        return DataProviders.SWP.getResourceQName(appData.resourceType).then((resource) => {
                            GQLClient = DataProviders.GraphQL.getNewGQLClient(
                                appData.graphId,
                                appData.tagId,
                                resource.qname
                            );

                            return DataProviders.UNIFIED.getDefaultSearch(
                                appData.resourceType,
                                GQLClient,
                                bypassDefault
                            );
                        });
                    } else {
                        return appState;
                    }
                }
            );
        }

        getDefaults
            .then((appState) => {
                let resourceType = null;

                if (appState) {
                    appData.filters = appState.filters || appState.searchFilters;
                    appData.columns = appState.columns || appState.searchColumns;
                    resourceType = appState.resourceType || appState.selectedResourceType;

                    //MH: an appState must provide a resourceType
                    if (!resourceType) {
                        throw 'BAD_APPSTATE';
                    }
                }

                initResultsApp(
                    {
                        GQLClient,
                        store,
                        ...appData,
                        resourceType,
                    },
                    element
                );
            })
            .catch((err) => {
                //Only retry once, if bypassDefault is true, don't attempt a recovery
                if (err === 'BAD_APPSTATE' && !bypassDefault) {
                    //Failed to load the default due to a bad state, try again without the defaults
                    searchHelper.handleBadDefaultState(err, () => {
                        initView(element, true);
                    });
                } else {
                    swa.populateModalDialog(`Restoring Default Search failed:  ${err}`, 'Operation failed', null);
                    element.append(err.message);
                    element.querySelector('.cp-preloader').remove();
                }
            });
        return;
    }
};

const sendHashToEditor = () => {
    let _search = '';
    if (location.search) {
        _search = location.search.replace('?', '');
        let _searches = _search
            .split('&')
            .filter((item) => item.indexOf('_editorClass') === -1 || item.indexOf('resource') === -1);
        _search = _searches.join('&');
        _search = '&' + _search;
    }

    window.location.href = `${location.origin}${
        location.pathname
    }?_editorClass=teamwork:SimpleFormEditorApplication${_search}&resource=${location.hash.slice(1)}`;
};

const initAllElements = () => {
    const elements = Array.from(document.querySelectorAll('[data-app-type=edg-search]'));
    elements.forEach(function(elem) {
        initView(elem);
    });
};

const hasHash = () => {
    if (location.hash) {
        sendHashToEditor();
    } else {
        initAllElements();
    }
};

const initApp = () => {
    document.addEventListener('readystatechange', (event) => {
        if (event.target.readyState === 'interactive') {
            const element = document.querySelector('[data-page-type*=results-editor-page]');

            if (element) {
                hasHash();
            } else {
                initAllElements();
            }
        }
    });
};

initApp();
