import React from 'react';
import { AppContext, ResourceTableCell, CreateResourceDialogs, ActionTable } from 'TB';
import * as FunctionUtils from 'FunctionUtils';
import * as TBUtils from 'utils';
import memoize from 'memoize-one';

export default class LogicalEntityRelationshipsGadget extends React.Component {
    constructor(props) {
        super(props);

        this.currentDataFetch = null;

        this.state = {
            data: [],
            loading: false,
        };
    }

    componentDidMount() {
        this.loadData();
        this.context.getEventManager().subscribe('edit', this.handleEdit);
    }

    componentWillUnmount() {
        if (this.currentDataFetch) {
            this.currentDataFetch.cancel();
        }

        this.context.getEventManager().unsubscribe('edit', this.handleEdit);
    }

    handleEdit = (edit) => {
        if (this.state.data) {
            let containsObject = this.state.data.some((d) => edit.containsSubject(d.uri));

            if (containsObject) {
                this.loadData();
            }
        }
    };

    //Given a cell for a resource, returns a ResourceTableCell
    renderCell = ({ cell: { value } }) => {
        let node = value;
        if (node) {
            return <ResourceTableCell resource={node} showURIs={this.props.showURIs} />;
        }

        return null;
    };

    loadData = () => {
        this.setState({ loading: true }, this.fetchData);
    };

    fetchData = () => {
        if (this.currentDataFetch) {
            this.currentDataFetch.cancel();
        }

        this.currentDataFetch = FunctionUtils.makeCancelable(
            this.context.swp({
                _viewClass: 'edg:DataTableResultsCallbackForLogicalEntityRelationships',
                resource: `<${this.props.focusNode.uri}>`,
            })
        );

        this.currentDataFetch.promise
            .then((response) => {
                this.setState({
                    data: response.data,
                    loading: false,
                });
            })
            .catch((err) => {
                console.error(err);
                this.currentDataFetch = null;
            });
    };

    getColumns = () => {
        let columns = [
            { Header: 'uri', show: false, accessor: 'uri' },
            {
                Header: 'Logical Relation',
                accessor: 'entity',
                filter: this.filterMethod,
                Cell: this.renderCell,
                width: 150,
            },
            {
                Header: 'has cardinality in',
                accessor: 'cardinality',
                filter: this.filterMethod,
                Cell: this.renderCell,
                width: 150,
            },
            {
                Header: 'Relation',
                accessor: 'relation',
                filter: this.filterMethod,
                Cell: this.renderCell,
                width: 125,
            },
            {
                Header: 'with cardinality',
                accessor: 'otherCardinality',
                filter: this.filterMethod,
                Cell: this.renderCell,
                width: 150,
            },
            {
                Header: 'on Entity',
                accessor: 'otherEntity',
                filter: this.filterMethod,
                Cell: this.renderCell,
                width: 150,
            },
        ];

        return columns;
    };

    setValues(values) {
        //no-op, required for the form contract
    }

    render() {
        return (
            <div className="box-rounded">
                <div className="size18px dark-blue">
                    Relationships for <strong>{this.props.focusNode.label}</strong>{' '}
                </div>
                <ActionTable
                    defaultSortColumn={3}
                    loading={this.state.loading}
                    reloadData={this.loadData}
                    columns={this.getColumns()}
                    data={this.state.data}
                    filterable={true}
                    actionButtons={this.getActionButtons()}
                />
            </div>
        );
    }

    onCreateNew = TBUtils.debounce((resource) => {
        if (resource) {
            this.loadData();
        }
    }, 250);

    getActionButtons = () => {
        return [
            {
                label: 'New',
                action: (e, tableInst) => {
                    CreateResourceDialogs.createInstance(
                        this.context,
                        { uri: 'http://edg.topbraid.solutions/model/LogicalRelation', label: 'Logical Relation' },
                        this.onCreateNew,
                        { focusNode: this.props.focusNode }
                    );
                },
            },
        ];
    };
    filterMethod = memoize((rows, id, filterValue) => {
        rows = rows.filter((row) => {
            const rowValue = row.values[id];

            return String(rowValue.label)
                .toLowerCase()
                .includes(String(filterValue).toLowerCase());
        });
        return rows;
    });
}

LogicalEntityRelationshipsGadget.score = (value, field) => {
    return null;
};

LogicalEntityRelationshipsGadget.contextType = AppContext;
