import React from 'react';
import { AppContext, AutoComplete, TBDialog, LabeledComponent, LangSelect, Edit } from 'TB';

const ScalarDatatypeUnion = {
    uri: 'edg:ScalarDatatypeUnion',
    label: 'Datatype',
};

const LogicalEntity = {
    uri: 'edg:LogicalEntity',
    label: 'Logical Entity',
};

const LOGICAL_ATTRIBUTE = 'http://edg.topbraid.solutions/model/LogicalAttribute';

export default class CreateLogicalAttributeDialog extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            enableOkButton: false,
            label: '',
            labelLang: '',
            entity: this.props.focusNode || null,
            datatype: null,
            isPrimaryKey: false,
            isForeignKey: false,
            isNullable: false,
        };
    }
    componentDidMount() {
        this.getIdentifer().then((id) => {
            this.setState({
                identifier: id,
            });
        });
    }

    getIdentifer = () => {
        let template = `SELECT ?identifier WHERE {
            BIND(edg:newResourceWithUUID(?resourceType, '') as ?identifier).
        }`;

        let q = {
            query: template,
            variables: {
                resourceType: { uri: LOGICAL_ATTRIBUTE },
            },
        };

        return this.context.sparqlQuery(q).then((response) => {
            return response.bindings[0].identifier.uri;
        });
    };

    getInfo = () => {
        return 'This dialog produces a new ' + this.props.type.label + '.';
    };

    getTitle = () => {
        return 'Create ' + this.props.type.label;
    };

    createMutation = () => {
        // CreateLogicalRelationHandler
    };

    onOK = () => {
        let params = {
            _base: this.context.getGraph().getQueryGraphURI(),
            uri: this.state.identifier,
            label: this.state.label,
            resourceType: LOGICAL_ATTRIBUTE,
            logicalEntity: this.state.entity.uri,
            datatype: this.state.datatype.uri,
            isPrimaryKey: this.state.isPrimaryKey,
            isForeignKey: this.state.isForeignKey,
            isNullable: this.state.isNullable,
            metadata: JSON.stringify({ createResource: this.state.identifier }),
        };

        if (this.state.labelLang && this.state.labelLang.length > 0) {
            params.labelLang = this.state.labelLang;
        }

        return swa
            .post('edg:CreateLogicalAttributeHandler', params)
            .then((response) => {
                let { data } = response;

                if (data.error) {
                    throw data.error;
                }

                if (data.violations && data.violations.length > 0) {
                    throw response.violations;
                }

                if (data.warnings && data.warnings.length > 0) {
                    throw response.warnings;
                }

                if (this.props.then) {
                    return this.props.then({ uri: this.state.identifier });
                }
            })
            .catch((err) => {
                this.context.showException(err);
            });
    };

    enableOkButton = () => {
        return Boolean(this.state.label && this.state.entity && this.state.datatype);
    };

    render() {
        return (
            <TBDialog
                title={this.getTitle()}
                info={this.getInfo()}
                enableOkButton={this.enableOkButton()}
                onOK={this.onOK}
                body={this.renderBody()}
            />
        );
    }

    renderBody = () => {
        return (
            <React.Fragment>
                <LabeledComponent label="Label:" info="">
                    <input
                        id="label-input"
                        autoComplete="off"
                        className="LabelInput form-control"
                        type="text"
                        onChange={this.updateLabel}
                        value={this.state.label}
                    />
                    <LangSelect onChange={this.updateLabelLang} value={this.state.labelLang} />
                </LabeledComponent>
                <LabeledComponent label="Entity:" info="">
                    <AutoComplete
                        type={LogicalEntity}
                        onSelect={this.handleEntityChange}
                        value={this.state.entity}
                        disabled={this.props.hasOwnProperty('focusNode')}
                    />
                </LabeledComponent>
                <LabeledComponent label="Datatype:" info="">
                    <AutoComplete
                        type={ScalarDatatypeUnion}
                        onSelect={this.handleDataTypeChange}
                        value={this.state.datatype}
                    />
                </LabeledComponent>
                <LabeledComponent label="Primary Key?" info="">
                    <input type="checkbox" checked={this.state.isPrimaryKey} onChange={this.handlePrimaryKeyChange} />
                </LabeledComponent>
                <LabeledComponent label="Foreign Key?" info="">
                    <input type="checkbox" checked={this.state.isForeignKey} onChange={this.handleForeignKeyChange} />
                </LabeledComponent>
                <LabeledComponent label="Nullable?">
                    <input type="checkbox" checked={this.state.isNullable} onChange={this.handleIsNullableChange} />
                </LabeledComponent>
                <LabeledComponent
                    label="Identifier:"
                    info="Identifiers for Logical Attribute instances are generated using a prefix followed by a UUID."
                >
                    <span title={this.state.identifer} className="text-info">
                        {this.state.identifier}
                    </span>
                </LabeledComponent>
            </React.Fragment>
        );
    };

    updateLabel = (e) => {
        this.setState({
            label: e.target.value,
        });
    };

    updateLabelLang = (lang) => {
        this.setState({
            labelLang: lang,
        });
    };

    handleEntityChange = (entity) => {
        this.setState({
            entity: entity,
        });
    };

    handleDataTypeChange = (datatype) => {
        this.setState({
            datatype: datatype,
        });
    };

    handlePrimaryKeyChange = (e) => {
        this.setState({
            isPrimaryKey: e.target.checked,
        });
    };

    handleForeignKeyChange = (e) => {
        this.setState({
            isForeignKey: e.target.checked,
        });
    };

    handleIsNullableChange = (e) => {
        this.setState({
            isNullable: e.target.checked,
        });
    };
}

CreateLogicalAttributeDialog.contextType = AppContext;
