// JavaScript file for TopBraid EDG QA
import { axiosInstance } from 'utils';

var edgqa = {
    // listeners

    init: function() {
        //    $(document).on('click', '.edgQAhomelink', function () {
        //      var response;
        //      edgqa.load('edg-qa:ProjectTypes');
        //     });

        $(document).on('click', '.edgQAhomelink', function() {
            edgqa.navigateTo('edg-qa:Report');
        });
    },

    load: function(pane) {
        console.log(pane);

        return axiosInstance
            .get(servletURL, {
                params: {
                    _snippet: true,
                    _viewClass: pane,
                    _contextdebug: true,
                },
            })
            .then((response) => {
                swa.load('edg-QA-MainCanvas', response.data);
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    navigateTo: function(resource) {
        window.location = swa.server + swa.servlet + '?_viewClass=' + resource;
    },

    /**
     * Clears all checkboxes
     */
    clearSelection: function(tableId) {
        $('.graphCheckbox').prop('checked', false);
        $('.graphRow').removeAttr('style');
    },

    /**
     * Select all checkboxes
     */
    selectAll: function(tableId) {
        $('.graphCheckbox').prop('checked', true);
    },

    /**
     * Filter rows based on a string
     *
     */

    filterRows: function(columnClass) {
        var $dialog = $('#resultsModalDialog'),
            searchString;

        $dialog = edgqa.addEmptyModalToPage('Filter');
        $dialog
            .find('.modal-body')
            .html(
                '<div><input class="form-control" name="edgqa-input-text" type="text" placeholder="enter text pattern"/></div>'
            );
        $dialog.modal('show');

        $dialog.on('shown.bs.modal', function() {
            var $input = $(this).find('input');
            $input.focus();
            $input.on('keypress', function(event) {
                if (event.keyCode === 13 || event.keyCode === 8) {
                    $dialog.modal('hide');
                }
            });
        });

        $dialog.on('hidden.bs.modal', function() {
            var searchString = $(this)
                .find('input')
                .val();
            edgqa.filterFunction(searchString, columnClass);
        });
    },

    filterFunction: function(searchString, columnClass) {
        $('td.' + columnClass).each(function(index, element) {
            //		   console.log(this);
            if (
                $(this)
                    .filter(':nth-of-type(2)')
                    .text()
                    .indexOf(searchString) !== -1
            ) {
                $(this)
                    .parent()
                    .find('.graphCheckbox')
                    .click();
            } else {
                //			   $(this).css('display', 'none');
                $(element)
                    .parent()
                    .css('display', 'none');
                //			   console.log(element) ;
            }
        });
    },

    /**
     * Delete All Selected
     *
     */
    deleteAll: function() {
        var checkedRows = $('.graphRow input:checked');
        checkedRows.each(function() {
            var $cbox = this,
                id = $cbox.id,
                $btn = $('#' + id)
                    .parent()
                    .siblings('[data-delete-container]')
                    .find('button');

            edgqa.dataService($btn, true);
        });
    },

    /**
     * Create All Selected
     *
     */
    createAll: function() {
        var checkedRows = $('.graphRow input:checked');
        checkedRows.each(function() {
            var $cbox = this,
                id = $cbox.id,
                $btn = $('#' + id)
                    .parent()
                    .siblings('[data-update-container]')
                    .find('button');

            edgqa.dataService($btn, true);
        });
    },

    handleGraphRoleCompletion: function(checkedRows) {
        var graphRole = $('#graph-role-selector')
            .find('option:selected')
            .val();

        // ensure we don't refer to the old selected value
        if ($('#graphRolesModalDialog').length) {
            $('#graphRolesModalDialog').remove();
        }

        checkedRows.each(function(index, element) {
            var $cbox = $(this),
                graph = $cbox.closest('[data-graph]').attr('data-graph');

            //    	   $('#progressMeter').val((index/checkedRows.length) * 100);

            var params = {
                _viewClass: 'edg-qa:UpdateGraphRole',
                _snippet: true,
                graph: graph,
                role: graphRole,
            };

            params = new URLSearchParams(params);

            utils.axiosInstance
                .post('swp', params)
                .then(() => {
                    edgqa.postActionService($cbox, graph, 'update');
                })
                .catch((err) => {
                    console.log(err);
                });

            $cbox.prop('checked', false);
        });
    },

    /**
     * sets the graph role for selected graphs
     *
     * see http://jsfiddle.net/boblauer/mVdgJ/
     */

    setGraphRole: function() {
        var checkedRows = $('.graphRow input:checked'),
            graphRoleModalInit;

        return axiosInstance
            .get('swp', {
                params: {
                    _viewClass: 'edg-qa:GraphRolesDialog',
                    _snippet: true,
                },
            })
            .then((response) => {
                graphRoleModalInit = function() {
                    var $graphRoleDialog = $(response.data),
                        deferred = new $.Deferred();

                    $graphRoleDialog
                        .on('click', '#set-graph-role-done', deferred.resolve)
                        .on('click', '#set-graph-role-cancelled', deferred.reject);

                    $graphRoleDialog.modal('show');

                    return deferred.promise();
                };

                var promise = graphRoleModalInit();

                promise.done(function() {
                    edgqa.handleGraphRoleCompletion(checkedRows);
                });

                promise.fail(function() {
                    console.log('cancel clicked');
                });
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    addEmptyModalToPage: function(title) {
        if ($('#resultsModalDialog').length) {
            $('#resultsModalDialog').remove();
        }

        return $(
            '<div class="modal fade" tabindex="-1" role="dialog" id="resultsModalDialog">' +
                '<div class="modal-dialog modal-lg" role="document">' +
                '<div class="modal-content">' +
                '<div class="modal-header">' +
                '<button type="button" class="btn btn-default close" data-dismiss="modal" aria-label="Close">' +
                '<span aria-hidden="true">&times;</span>' +
                '</button>' +
                '<h4 class="modal-title">' +
                title +
                '</h4>' +
                '</div>' +
                '<div class="modal-body">' +
                '</div>' +
                '<div class="modal-footer">' +
                '<button type="button" class="btn btn-default" data-dismiss="modal">Done</button>' +
                '</div>' +
                '</div>' +
                '</div>' +
                '</div>'
        );
    },

    doUpdateStuff: function($btn) {
        $btn.text('Update').attr('disabled', false);
        $btn.parent()
            .siblings('[data-delete-container]')
            .find('button')
            .attr('disabled', false);
    },

    doDeleteStuff: function($btn) {
        $btn.parent()
            .siblings('[data-update-container]')
            .find('button')
            .text('Create');
        $btn.attr('disabled', 'disabled');
    },

    /**
     * postUpdate
     *
     * execute any refresh needed on row cells
     */

    postActionService: function($btn, graph, operationKind) {
        var $postServiceContainers = $btn.parent().siblings('[data-post-service]');

        $postServiceContainers.each(function(index, element) {
            var $postServiceContainer = $(this),
                postService = $postServiceContainer.attr('data-post-service');

            axiosInstance.interceptors.request.use(
                function(config) {
                    swa.insertLoadingIndicator($postServiceContainer);
                    return config;
                },
                function(error) {
                    return Promise.reject(error);
                }
            );

            axiosInstance
                .get('swp', {
                    params: {
                        _viewClass: postService,
                        _snippet: true,
                        graph: graph,
                    },
                })
                .then((response) => {
                    $postServiceContainer.html(response.data);

                    if (operationKind === 'update') {
                        edgqa.doUpdateStuff($btn);
                    } else {
                        edgqa.doDeleteStuff($btn);
                    }
                })
                .catch((err) => {
                    $postServiceContainer.html(err);
                });
        });
    },

    /**
     * dataService
     *
     * execute a data service specified on a button
     */
    dataService: function(element, batchMode) {
        var $btn = element,
            $dialog = $('#resultsModalDialog'),
            service = $btn.attr('data-service'),
            graph = $btn.closest('[data-graph]').attr('data-graph'),
            $request,
            operationKind = $btn.is('[data-service*="Update"]') ? 'update' : 'delete';

        $btn.attr('disabled', 'disabled');

        axiosInstance
            .get(swa.servlet, {
                params: {
                    _viewClass: service,
                    _snippet: true,
                    graph: graph,
                    editing: false,
                },
            })
            .then((response) => {
                if (batchMode === false && operationKind === 'update') {
                    $dialog = edgqa.addEmptyModalToPage('Resource');
                    $dialog.find('.modal-body').html(response.data);
                    $dialog.modal('show');
                }
            })
            .catch((err) => {
                console.log(err);
            })
            .then(function() {
                edgqa.postActionService($btn, graph, operationKind);
            });
    },

    /**
     * Executes an SWP script on the server.
     * The script must be globally registered (using an .sms. file).
     * @param script  the qname of the script
     * @param params  the parameters
     * @param callback  a JavaScript expression that shall be executed when the script
     *                  finishes. Can access the result of the script with the variable 'data'.
     */
    execSWP: function(script, params) {
        var params = '_viewClass=' + script + params;
        var results;

        return axiosInstance
            .get('swp', {
                params: params,
            })
            .then((response) => {
                return response.data;
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    /**
     * buttonDataService wraps a call to edgqa.dataService
     * with batchMode set to false
     */
    buttonDataService: function() {
        edgqa.dataService($(this), false);
    },

    /**
     * dataTableRowsSelected wraps a call to edg.tableRowsSelected
     */

    dataTableRowsSelected: function(service, data) {
        return axiosInstance
            .get(swa.servlet, {
                params: {
                    _snippet: true,
                    _viewClass: service,
                    data: data,
                },
            })
            .then((response) => {
                console.log(response.data);
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },
};

(function($) {
    var attachEventListeners = function(idx, element) {
        var $item = $(this);

        $item.on('click', function(event) {
            //			event.preventDefault();

            var index = parseInt(idx) * 2 + 2;

            $('#accordion')
                .find('[aria-controls="ui-id-' + index + '"]')
                .click();
        });
    };

    $(document).ready(function() {
        var $select = $('.chosen-select');

        $select.chosen({
            disable_search_threshold: 8,
            no_results_text: 'No results found for',
        });

        $select.on('change', function() {
            var $selectMenu = $(this),
                family = $selectMenu.val(),
                //			    graph = $selectMenu.val.attr('data-datatype-family');

                graph = $('option:selected', this).attr('data-datatype-family');

            $('#edg-qa-datatype-tests')
                .empty()
                .append('Family is ' + family + ' graph is ' + graph);

            //			edgExporter.avro($selectMenu.val(), projectGraph);
        });

        $('#ProjectTypesList')
            .find('li')
            .each(attachEventListeners);

        $(document).on('click', '[data-service]', edgqa.buttonDataService);
    });
})(jQuery);

export default edgqa;
